<template>
  <div class="home-view">
    <div class="header">
      <h1>SmartBook Analysis Platform</h1>
      <p class="subtitle">Interactive analysis and visualization of critical events and topics</p>
    </div>
    
    <div class="search-section">
      <div class="search-container">
        <input 
          v-model="searchQuery" 
          type="text" 
          placeholder="Search analysis pages..." 
          class="search-input"
          @input="filterPages"
        />
        <div class="search-stats">
          Showing {{ filteredPages.length }} of {{ allPages.length }} pages
        </div>
      </div>
    </div>
    
    <div class="category-filter">
      <button 
        v-for="category in categories" 
        :key="category"
        @click="selectedCategory = category"
        :class="['category-btn', { active: selectedCategory === category }]"
      >
        {{ category }} ({{ getCategoryCount(category) }})
      </button>
    </div>

    <div class="pages-grid">
      <div v-for="page in paginatedPages" :key="page.filename" class="page-card">
        <div class="page-header">
          <span class="category-badge" :class="getCategoryClass(page.category)">
            {{ page.category }}
          </span>
        </div>
        <h3 class="page-title">{{ page.title }}</h3>
        <div class="page-actions">
          <button @click="loadPage(page.filename)" class="view-btn">
            <i class="icon">üìÑ</i>
            View Analysis
          </button>
        </div>
      </div>
    </div>
    
    <!-- Pagination -->
    <div v-if="totalPages > 1" class="pagination">
      <button 
        @click="currentPage = Math.max(1, currentPage - 1)"
        :disabled="currentPage === 1"
        class="page-btn"
      >
        ‚Üê Previous
      </button>
      
      <span class="page-info">
        Page {{ currentPage }} of {{ totalPages }}
      </span>
      
      <button 
        @click="currentPage = Math.min(totalPages, currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="page-btn"
      >
        Next ‚Üí
      </button>
    </div>
    
    <!-- Modal for page content -->
    <div v-if="selectedPageContent" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h2>{{ selectedPageTitle }}</h2>
          <button @click="closeModal" class="close-btn">√ó</button>
        </div>
        <div class="modal-body">
          <div v-html="selectedPageContent" class="content"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PagesService from '@/services/pagesService'

export default {
  name: 'Home',
  data() {
    return {
      allPages: [],
      filteredPages: [],
      selectedCategory: 'All',
      searchQuery: '',
      selectedPageContent: null,
      selectedPageTitle: '',
      categories: ['All'],
      currentPage: 1,
      pageSize: 12
    }
  },
  computed: {
    paginatedPages() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredPages.slice(start, end)
    },
    totalPages() {
      return Math.ceil(this.filteredPages.length / this.pageSize)
    }
  },
  async mounted() {
    this.allPages = await PagesService.getAllPages()
    this.filteredPages = this.allPages
    
    // Extract unique categories
    const uniqueCategories = [...new Set(this.allPages.map(page => page.category))]
    this.categories = ['All', ...uniqueCategories.sort()]
  },
  methods: {
    filterPages() {
      let pages = this.allPages
      
      // Filter by category
      if (this.selectedCategory !== 'All') {
        pages = pages.filter(page => page.category === this.selectedCategory)
      }
      
      // Filter by search query
      if (this.searchQuery.trim()) {
        pages = PagesService.searchPages(pages, this.searchQuery)
      }
      
      this.filteredPages = pages
      this.currentPage = 1 // Reset to first page when filtering
    },
    getCategoryCount(category) {
      if (category === 'All') {
        return this.allPages.length
      }
      return this.allPages.filter(page => page.category === category).length
    },
    getCategoryClass(category) {
      if (category.includes('Turkey') || category.includes('Earthquake')) return 'earthquake'
      if (category.includes('Ukraine')) return 'ukraine'
      if (category.includes('COVID')) return 'covid'
      return 'general'
    },
    async loadPage(filename) {
      try {
        const content = await PagesService.loadPageContent(filename)
        this.selectedPageContent = PagesService.markdownToHtml(content)
        this.selectedPageTitle = this.allPages.find(p => p.filename === filename)?.title || 'Analysis'
      } catch (error) {
        console.error('Error loading page:', error)
        this.selectedPageContent = '<p>Error loading page content. Please try again.</p>'
        this.selectedPageTitle = 'Error'
      }
    },
    closeModal() {
      this.selectedPageContent = null
      this.selectedPageTitle = ''
    }
  },
  watch: {
    selectedCategory() {
      this.filterPages()
    }
  }
}
</script>

